import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Funciones } from '../compartido/funciones';
import { environment as env } from 'src/environments/environment';
import { ApiMongoService } from '../services/apiMongo.service';
import * as $ from 'jquery';
import { IonSlides, ModalController } from '@ionic/angular';
import { StorageService } from '../services/storage.service';
import { IonicComponentsService } from '../services/ionic-components.service';
import { DataService } from '../services/data-service.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  constructor(
    public _data :DataService,
    public _comp: IonicComponentsService,
    private _storage: StorageService,
    private _modal: ModalController,
    private _fun: Funciones,
    private _apiMongo: ApiMongoService,
    private fun: Funciones,
    public router: Router) {
    this.dataCred();
    /*     this.getUser(); */
  }

  data: any;
  user: any;
  page = 'info';

  //COMPONENT INFO LOTE
  trace: any = {
    credentialSubject: {
      details: [],
      header: {
        attributes: {
          country_source: "",
          country_source_key: "",
          country_target: "",
          country_target_key: "",
          delivery_date: null,
          notes: "",
          order_date: null,
          product_type: "",
          product_type_key: "",
          quantity: 0,
          reference_key: "",
          sku: "",
          unit: "",
          via_type: "",
          via_type_key: "",
        }
      }
    }
  }

  //COMPONENT TRAZABILIDAD
  traceDetail = [];

  // http://localhost:8100/#/code?id=f8d9a7bba7532e9aef581b09a9ed27de60fbbcfe90d56454765e6ba639fe3649&i=1
  //http://rastrar.com/appComprador/#/code?id=f8d9a7bba7532e9aef581b09a9ed27de60fbbcfe90d56454765e6ba639fe3649&i=1

  //COMPONET ALAMCEN
  producersLot = [];

  ionViewDidEnter() {
    this.getUser();
  }

  //http://localhost:8100/appComprador/#/code?id=d2e24c20484f65eeae0fb87e28987cceccb1e051158411bf5dfbc0cd41c9540f&i=1



//   http://localhost:8100/appComprador/#/code?id=2e75f3397766a04c55bfd66be20e0fbe6d8e58af7d1a7c159e3635c55c7e581d&i=1
//   http://rastrar.com/appComprador/#/code?id=2e75f3397766a04c55bfd66be20e0fbe6d8e58af7d1a7c159e3635c55c7e581d&i=1
//   http://rastrar.com/appComprador/#/code?id=2e75f3397766a04c55bfd66be20e0fbe6d8e58af7d1a7c159e3635c55c7e581d&i=1

  async getUser() {
    this.user = await this._storage.getLocalStorage('USER');
  }

  async dataCred() {
    this.data = await this.validCred();
    this._data.dataSend.next(this.data);
  }


  async permisoLogin(page) {

    if (this.user == 'INVITADO') {
      let res = await this.fun.initSesion();
      if (this.fun.isVarInvalid(res)) return;
      await this._storage.remove('USER');
      this.router.navigate(['/login']);
    } else this.page = page;

  }

  async validCred() {
    return new Promise(async (resolve, reject) => {
      let loading: any = await this._comp.presentLoading();
      let codeLote = await this._storage.getLocalStorage('CODE');

      if (this._fun.isVarInvalid(codeLote)) {
        loading.dismiss();
        this.router.navigate(['/notfound']);
        return;
      }

      let filter = {
        "$and": [
          { "data.correlative1": { "$lte": Number(codeLote.index) } },
          { "data.correlative2": { "$gte": Number(codeLote.index) } }
        ]
      };

      const res: any = await this._apiMongo.filter(env.COLLECTION.associateQr, codeLote.code, filter);
      console.log('res.result', res.result);
      if (this._fun.isEmpty(res.result)) {
        loading.dismiss();
        this.router.navigate(['/notfound']);
        await this._fun.alertError('Código incorrecto');
        return;
      }

      let trxid = res.result[0][codeLote.code][0].key;
      let dni = res.result[0][codeLote.code][0].data.indentUser;

      const res_cred: any = await this._apiMongo.getkey(env.COLLECTION.dataQrUser, dni, trxid);

      console.log('res_cred', res_cred);


      if (this._fun.isEmpty(res_cred.result)) {
        loading.dismiss();
        this.router.navigate(['/notfound']);
        await this._fun.alertError('Código incorrecto');
        return;
      }

      let dataCred = res_cred.result[0][dni][0];
      loading.dismiss();
      resolve(dataCred);
    });

  }

}
